// messenger
.massenger{
    margin-top: 60px;
    height:84vh;
    overflow: hidden;
    .container-fluid{
        padding: 0px 220px;
    }
}
// messenger user list
.message-list-box{
    border: 1px solid hsl(var(--base-border-color));
    .massenger-header{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 20px;
        box-shadow: rgba(0, 0, 0, 0.04) 0px 3px 5px;
        height: 140px;
        .logo{
            max-width: 120px;
        }
        .msg-serch-box{
            position: relative;
            i{
                position: absolute;
                top: 30%;
                right: 26px;
            }
        }
    }
    .message-list{
        margin: 8px 0px 0px 0px;
        padding: 0px 14px;
        height: 64vh;
        overflow-y: scroll;
        a{
            width: 100%;
        }
        .msguser-card{
            padding: 14px 6px;
            display: flex;
            align-items: center;
            gap: 10px;
            border-radius: 4px;
            .user-thumb{
                height: 50px;
                width: 50px;
                position: relative;
                i{
                    position: absolute;
                    bottom: 0;
                    right: 4px;
                    font-size: 10px;
                    color: hsl(var(--base));
                    z-index: 1;
                }
               
                img{
                    
                    height: 100%;
                    width: 100%;
                    border-radius: 50%;
                    object-fit: cover;
                }
            }
            .user-name{
                font-size: 20px;
                
                .active{
                    color: hsl(var(--base));
                }
                i{
                    font-size: 6px;
                }
            }
            .present-msg{
                color: hsl(var(--black)/0.5);
                font-size: 14px;
            }

            &:hover{
                background: hsl(var(--base)/0.1);
            }
        }
    }::-webkit-scrollbar {
        width: 4px;
      }
      ::-webkit-scrollbar-track {
        box-shadow: inset 0 0 5px hsl(var(--base)/.2); 
        
      }
      ::-webkit-scrollbar-thumb {
        background: hsl(var(--base)/.5); 
        
      }
      ::-webkit-scrollbar-thumb:hover {
        background: hsl(var(--base)); 
      }
}

// messenger text body
.message-main-body{
    .message-body-header{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 10px 40px;
        border: 1px solid hsl(var(--base-border-color));
        .user-profile{
            display: flex;
            align-items: center;
            gap: 10px;
            .user-thumb{
                height: 40px;
                width: 40px;
               
                img{
                    position: relative;
                    height: 100%;
                    width: 100%;
                    border-radius: 50%;
                    object-fit: cover;
                }
            }
            .user-name{
                font-size: 14px;
            }
            .user-status{
                font-size: 12px;
                color: hsl(var(--black)/0.5);
            }
        }

        .msg-chat-control{
            position: relative;
            display: flex;
            gap: 12px;
            i{
                cursor: pointer;
            }
            .setting-dropdown{
                border: 1px solid hsl(var(--base-border-color));
                position: absolute;
                padding: 14px;
                width: 120px;
                top: 28px;
                right: 0;
                background: hsl(var(--white));
                display: none;
                ul{
                    li{
                        a{
                            display: inline-flex;
                            justify-content: flex-start;
                            align-items: center;
                            gap: 12px;
                            i{
                                width: 16px;
                            }
                        }
                    }
                }
            }
        }
    }

    .msg-list-wraper{
        height: 64vh;
        overflow-y: scroll;
        .message-text{
            box-sizing: border-box;
            padding: 8px 16px;
            margin: 16px;
            background: hsl(var(--white));
            border-radius: 8px 8px 8px 0;
            min-height: 32px;
            width: -webkit-fit-content;
            width: -moz-fit-content;
            width: fit-content;
            max-width: 66%;
            box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
            font-size: 14px;
            line-height: 1.5;
            word-break: break-word;
            .text{
                p{
                    font-size: 16px;
                }
            }
            .msg-text-meta{
                padding: 4px 0px;
                display: flex;
                flex-direction: row;
                gap: 16px;
                font-size: 12px;
                .text-dt{
                    color:hsl(var(--grey));
                }
                span{
                    i{
                        color: hsl(var(--base));
                    }
                }
               
            }
           
        }
    
        .odd{
            margin: 16px 16px 16px auto;
            border-radius: 4px 4px 0 4px;
            background: hsl(var(--base)/.05);
            text-align: left;
           
        }
        .msg-time{
            font-size: 11.2px;
            background: hsl(var(--white));
            padding: 0 16px;
            border-radius: 32px;
            color: hsl(var(--grey));
            width: -webkit-fit-content;
            width: -moz-fit-content;
            width: fit-content;
            margin: 16px auto;
            line-height: 27px;
        }
    }::-webkit-scrollbar {
        width: 4px;
      }
      ::-webkit-scrollbar-track {
        box-shadow: inset 0 0 5px hsl(var(--base)/.2); 
        
      }
      ::-webkit-scrollbar-thumb {
        background: hsl(var(--base)/.5); 
        
      }
      ::-webkit-scrollbar-thumb:hover {
        background: hsl(var(--base)); 
      }

    
}

.msg-setting-show{
    .setting-dropdown{
        display: block !important;
    }
}

// messenger input
.msg-btm{
    border: 1px solid hsl(var(--base-border-color));
    padding: 0px 40px;
   .msg-input{
    position: relative;
    padding: 10px 0px;
    width: 100%;
    form{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        gap: 12px;

        .attchment-input-box{
            position: relative;
            i{
                cursor: pointer;
                font-size: 24px;
            }
            .att-file-name{
                position: absolute;
                width: 200px;
                left: 0;
                top: -70px;
                color: hsl(var(--base));
                font-size: 10px;
                
            }
        }

        button{
            position: absolute;
            right: 20px;
            font-size: 24px;
            color: hsl(var(--base));
        }
    }
        .emojionearea{
            border: 1px solid hsl(var(--base)) !important;
            border-radius: 8px !important;
            .emojionearea-editor {
            min-height: 4em !important;
            max-height: 0em !important;
            border: 1px solid transparent !important;
            border-radius: 8px !important;
            padding: 17px 50px 6px 50px !important;
        }
        .form-control {
        max-height: 6vh!important;
        border: 1px solid hsl(var(--base)) !important;
        }
        .emojionearea-button {
            z-index: 5;
            position: absolute;
            left: 10px;
            top: 15px;
            width: 24px;
            height: 24px;
            opacity: 1;
            cursor: pointer;
            -moz-transition: opacity .3s ease-in-out;
            -o-transition: opacity .3s ease-in-out;
            -webkit-transition: opacity .3s ease-in-out;
            transition: opacity .3s ease-in-out;
        }
    } 
    .emojionearea-picker{
        .emojionearea-filters {
            background: hsl(var(--base)/0.5);
        }
    }
    .emojionearea .emojionearea-picker.emojionearea-picker-position-top {
      
        left: 10px !important;
    }
    .emojionearea .emojionearea-picker.emojionearea-filters-position-top.emojionearea-search-position-top .emojionearea-scroll-area {
        bottom: 0;
        
    }::after{
        left: 20px !important;
    }
    
    }
   
}

.open-message-list-btn{
    display: none;
}

@media(max-width:769px){
    .massenger{
        margin-top: 65px;
        height:91vh;
    }
    .open-message-list-btn{
        background: hsl(var(--base));
        display: block !important;
        position: absolute;
        top: 50%;
        left: 0px;
        z-index: 99 !important;
        padding: 12px;
        border-radius: 0px 50px 50px 0px;
        i{
            color: hsl(var(--white));
        }
    }
    .massenger-section{
        height: 90vh; 
    }
    .msg-list-wraper{
        height: 74vh !important;
    }
    .message-list-box{
        display: none;
    }
    .message-list-show{
        .message-list-box{
            display: block !important;
            position: absolute;
            z-index: 9;
            background: hsl(var(--white));
            max-width: 250px;
        }
        .message-list{
            height: 73.5vh !important;
        }
    }
}

@media(max-width:426px){
    .msg-btm{
        .msg-input{
            .emojionearea{
                .emojionearea-editor {
                min-height: 3em !important;
                }
                .emojionearea-button {
                    top: 8px;
                }
            }
            .emojionearea .emojionearea-picker.emojionearea-picker-position-top {
                left: -84px !important;
            } 
        }
    }
    .msg-list-wraper{
        height: 66vh !important;
    }
    .message-list-show .message-list {
        height: 64vh !important;
    }
    .msg-btm .msg-input form button {
        font-size: 16px;
    }
    .msg-btm .msg-input form .attchment-input-box i {
        font-size: 16px;
    }
    .msg-btm .msg-input .emojionearea .emojionearea-editor {
        padding: 12px 50px 6px 50px !important;
    }
}

@media(max-width:1440px){
    .massenger{
        .container-fluid{
            padding: 0px;
        }
    }
}

@media(max-width:768px){
    .massenger{
        padding-top: 10px;
    }
}

@media(max-width:425px){
    .massenger{
        padding-top: 40px;
    }
}