/*====================== Preloader Style ====================*/
#loading{
	background-color: hsl(var(--base));
	height: 100%;
	width: 100%;
	position: fixed;
	z-index: 99;
	margin-top: 0px;
	top: 0px;
}
#loading-center{
	width: 100%;
	height: 100%;
	position: relative;
	}
	

#loading-center-absolute {
    display: flex;
    position: absolute;
    left: 50%;
    top: 50%;
    height: 150px;
    width: 150px;
    margin-top: -75px;
    margin-left: -75px;
    justify-content: center;
    align-items: center;
}


.loader-object {
	// width: 60px;
	// height: 60px;
	// background-color:#FFF;
	// position: absolute;
	// left: 65px;
	// top: 65px;
	// background:
	//   linear-gradient(#0000 calc(1*100%/6),#fff 0 calc(3*100%/6),#0000 0),
	//   linear-gradient(#0000 calc(2*100%/6),#fff 0 calc(4*100%/6),#0000 0),
	//   linear-gradient(#0000 calc(3*100%/6),#fff 0 calc(5*100%/6),#0000 0);
	// background-size:10px 400%;
	// background-repeat: no-repeat;
	// animation:matrix 1s infinite linear;
	width: 54px;
	height: 54px;
	border-radius: 50%;
	display: inline-block;
	position: relative;
	border: 4px solid hsl(var(--white));
	box-sizing: border-box;
	animation: rotation 1s linear infinite;
	&::after{
		content: '';  
		box-sizing: border-box;
		position: absolute;
		left: 4px;
		top: 4px;
		border: 3px solid hsl(var(--dark));
		width: 16px;
		height: 16px;
		border-radius: 50%;
	}
}
@keyframes rotation {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(360deg);
	}
	} 