/*====================== Button Style ====================*/
button {
    border: none;
    transition: .2s linear;
    &:focus {
      outline: none;
      box-shadow: none;
    }
  }
// Custom Button
.btn {
    &:focus {
        outline: none;
        box-shadow: none;
      }
      color: hsl(var(--white));
      font-weight: 500;
      padding: 8px 20px;
      border-radius: 4px;
      position: relative;
      z-index: 1;
      text-align: center;
      border: 1px solid hsl(var(--base)) !important;   
      text-transform: capitalize;
      font-weight: 500;
      transition: .5s;
      font-family: var(--heading-font);
      font-size: 16px;
      overflow: hidden;
        // &::before {
        //     position: absolute;
        //     content: "";
        //     height: 100%;
        //     width: 0px;
        //     background-color: hsl(var(--white));
        //     top: 0;
        //     right: 50%;
        //     z-index: -1;
        //     border-radius: 6px;
        //     transition: .5s;
        // }
        // &::after {
        //     position: absolute;
        //     content: "";
        //     height: 100%;
        //     width: 0px;
        //     background-color: hsl(var(--white));
        //     top: 0;
        //     left: 50%;
        //     z-index: -1;
        //     border-radius: 6px;
        //     transition: .5s;
        // }
        @include slg-screen {
            padding: 10px 18px;
        }
        @include sm-screen {
            padding: 11px 25px;
        }
        @include msm-screen {
                padding: 6px 7px;
        }
      &:hover, &:focus {
          color: hsl(var(--white));
          border-color: transparent;
          border-radius: 6px;
          border: 1px solid hsl(var(--base));
      }
      &--lg {
        padding: 15px 35px;
        @include sm-screen {
            padding: 13px 30px;
        }
        @include msm-screen {
            padding: 11px 25px;
        }
      }
      &--sm {
        padding: 4px 12px;
        background-color: hsl(var(--base));
        color: hsl(var(--white));
        font-size: 14px;
        &:hover, &:focus {
            background-color: hsl(var(--white));
            border: 1px solid hsl(var(--base));
            color: hsl(var(--base)) !important;
          
        }
        @include sm-screen {
            padding: 5px 8px;
        }
      }
      &--ssm {
        padding: 2px 10px;
        background-color: hsl(var(--base));
        color: hsl(var(--white));
        font-size: 12px;
        &:hover, &:focus {
            background-color: hsl(var(--white));
            border: 1px solid hsl(var(--base));
            color: hsl(var(--base));
        }
        @include sm-screen {
            padding: 3px 8px;
        }
      }
      &--icon {
        width: 35px;
        height: 35px;
        line-height: 35px;
        font-size: 15px;
        padding: 0;
    }
    // ============= Different Color Button Start ==================
    &--base {
        background-color: hsl(var(--base));
        color: hsl(var(--white));
        &.style-1{
            background-color: hsl(var(--white));
            color: hsl(var(--base));
            &::before {
                background-color: hsl(var(--base));
            }
            &:hover {
                color: hsl(var(--white));

            }
        }
        &:hover::before {
            height: 100%;
            width: 50%;
            transform: rotate(180deg);
        }
        &:hover::after {
            height: 100%;
            width: 50%;
            transform: rotate(180deg);
        }
        &:hover, &:focus {
            background-color: hsl(var(--white));
            border: 1px solid hsl(var(--base));
            color: hsl(var(--base)) !important;
            border-radius: 6px;
            
        }
        &.pill {
            border-radius: 35px;
        }
        &.outline {
            background-color: transparent;
            border: 1px solid hsl(var(--base));
            color: hsl(var(--base));
            border-radius: 4px;
            &.hover-white-c {
                &:hover, &:focus{
                    color: hsl(var(--white));
                }
            }
            &:hover, &:focus{
                background-color: hsl(var(--base));
                color: hsl(var(--white)) !important;
            }
        }
    }
    &--simple {
        &__icon {
            font-size: 13px;
            margin-left: 5px;
        }
    }
    &--primary {
        background-color: hsl(var(--primary));
        &:hover, &:focus {
            background-color: hsl(var(--primary-d-100)); 
        }
        &.pill {
            border-radius: 35px;
        }
        &.outline {
            border: 1px solid hsl(var(--primary));
            background-color: transparent;
            color: hsl(var(--primary));
            &:hover, &:focus {
                background-color: hsl(var(--primary));
                color: hsl(var(--white));
            }
        }
    }
    &--secondary {
        background-color: hsl(var(--secondary));
        &:hover, &:focus {
            background-color: hsl(var(--secondary-d-100)); 
        }
        &.pill {
            border-radius: 35px;
        }
        &.outline {
            border: 1px solid hsl(var(--secondary));;
            background-color: transparent;
            color: hsl(var(--black));
            &:hover, &:focus {
                background-color: hsl(var(--secondary));;
                color: hsl(var(--white));
            }
        }
    }
    &--success {
        background-color: hsl(var(--success));
        &:hover, &:focus {
            background-color: hsl(var(--success-d-100)); 
        }
        &.pill {
            border-radius: 35px;
        }
        &.outline {
            border: 1px solid hsl(var(--success));;
            background-color: transparent;
            color: hsl(var(--success));
            &:hover, &:focus {
                background-color: hsl(var(--success));;
                color: hsl(var(--white));
            }
        }
    }
    &--danger {
        background-color: hsl(var(--danger));
        &:hover, &:focus {
            background-color: hsl(var(--danger-d-100)); 
        }
        &.pill {
            border-radius: 35px;
        }
        &.outline {
            border: 1px solid hsl(var(--danger));;
            background-color: transparent;
            color: hsl(var(--danger));
            &:hover, &:focus {
                background-color: hsl(var(--danger));;
                color: hsl(var(--white));
            }
        }
    }
    &--warning {
        background-color: hsl(var(--warning));
        &:hover, &:focus {
            background-color: hsl(var(--warning-d-100)); 
        }
        &.pill {
            border-radius: 35px;
        }
        &.outline {
            border: 1px solid hsl(var(--warning));;
            background-color: transparent;
            color: hsl(var(--warning));
            &:hover, &:focus {
                background-color: hsl(var(--warning));;
                color: hsl(var(--white));
            }
        }
    }
    &--info {
        background-color: hsl(var(--info));
        &:hover, &:focus {
            background-color: hsl(var(--info-d-100)); 
        }
        &.pill {
            border-radius: 35px;
        }
        &.outline {
            border: 1px solid hsl(var(--info));;
            background-color: transparent;
            color: hsl(var(--info));
            &:hover, &:focus {
                background-color: hsl(var(--info));;
                color: hsl(var(--white));
            }
        }
    }
    &--violet {
        background-color: hsl(var(--violet));
        &:hover, &:focus {
            background-color: hsl(var(--violet-d-100)); 
        }
        &.pill {
            border-radius: 35px;
        }
        &.outline {
            border: 1px solid hsl(var(--violet));;
            background-color: transparent;
            color: hsl(var(--violet));
            &:hover, &:focus {
                background-color: hsl(var(--violet));;
                color: hsl(var(--white));
            }
        }
    }
}

/*========= Very small btn ======== */
.btn--small {
    color: hsl(var(--base));
    font-weight: 700;
    padding: 3px 10px;
    border-radius: 4px;
    position: relative;
    z-index: 1;
    text-align: center;
    border: 1px solid hsl(var(--base));
    text-transform: capitalize;
    font-weight: 600;
    transition: 0.5s;
    font-family: var(--heading-font);
    font-size: 13px;
    transition: 0.5s;
    &:hover, .btn--small:focus {
        background-color: hsl(var(--base));
        border: 1px solid hsl(var(--base));
        color: hsl(var(--white));
        border-radius: 6px;
    }
}

// ============= Different Color Button End ==================

// main btn
.base-btn{
    position: relative;
    transition: background 400ms;
    color: hsl(var(--base));
    border: 1px solid hsl(var(--base)) !important;
    padding: 4px 16px ;
    font-size: 20px;
    outline: 0;
    cursor: pointer;
  &:hover{
        background-color: hsl(var(--base)/.2);
    }
  }

  @media (max-width: 1399px) {
    .base-btn {
        font-size: 14px;
    }
  }

// contact btn
  .contact-btn{
    position: relative;
    transition: background 400ms;
    color: hsl(var(--base));
    border: 1px solid hsl(var(--base)) !important;
    padding: 6px 20px ;
    font-size: 20px;
    outline: 0;
    cursor: pointer;
  &:hover{
        background-color: hsl(var(--base)/.2);
    }
  }

  @media (max-width: 1399px) {
    .base-btn {
        font-size: 14px;
    }
  }
// btn ripple
  .ripple {
    background-position: center;
    transition: background 0.5s;
  }
  .ripple:hover {
    background: hsl(var(--base)/.1) radial-gradient(circle, transparent 1%, hsl(var(--base)/.3) 1%) center/15000%;
  }
  .ripple:active {
    background-color: hsl(var(--base)/.3);
    background-size: 100%;
    transition: background 0s;
  }
  

// card btn
  .card-actn__btn{
    color: hsl(var(--base));
    border: 1px solid hsl(var(--base)/.4) !important;
    padding: 3px 10px 5px;
    font-size: 14px;
    outline: 0;
   
  cursor: pointer;
  &:hover{
        background-color: hsl(var(--base));
        color: hsl(var(--white));
    }
  }

  
 
//   tap btn
.tab-btn{
    position: relative;
    transition: background 400ms;
    color: hsl(var(--base));
    border: 1px solid hsl(var(--base)) !important;
    padding: 0px 0px ;
    font-size: 20px;
    outline: 0;
    box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.3); /* black with 30% opacity */
    cursor: pointer;
  &:hover{
        background-color: hsl(var(--base)/.09);
    }
  }

// price btn

.price-btn{
    position: relative;
    transition: background 400ms;
    color: hsl(var(--base));
    border: 1px solid hsl(var(--base)) !important;
    padding: 6px 24px ;
    font-size: 20px;
    outline: 0;
    cursor: pointer;
  &:hover{
        background-color: hsl(var(--base)/.2);
    }
    i{
        margin-left: 8px;
    }
  }