
/*================ Top Header Start Here ================*/
.header-main-area {
    position: relative;
    top: 0;
    left: 0;
    right: 0;
    transition: 0.3s linear;
    padding-top: 0;
}


.header-top{
    border-bottom: 1px solid hsl(var(--base) / .08);
    padding:10px 0px;
    position: relative;
    z-index: 2;

    @include md-screen {
        display: none;
    }
    
    .social-list.style-two .social-list__link {
        width: 30px;
        height: 30px;
        font-size: 12px;
    }
}
.top-header-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    @include md-screen {
        justify-content: right;
    }
    
    @include xsm-screen {
        justify-content: center;
    }
}


.contact-list {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: 0 -20px;
    @include md-screen {
        justify-content: center;
    }
    @include sm-screen {
        display: none;
    }
    &__item {
        position: relative;
        color: hsl(var(--black));
        margin-left: 25px;
        &:last-child::before {
            display: none;
        }
        &::before {
            position: absolute;
            content: "";
            width: 1px;
            height: 15px;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            background-color: hsl(var(--white) /.4);
        }
        &-icon {
            font-size: 16px;
            text-align: center;
            color: hsl(var(--black));
            display: inline-block;
            margin-right: 10px;
        }
    }
    &__link {
        color: hsl(var(--black));
        font-size: 15px;
        font-weight: 400;
        font-family: var(--body-font);
        &:hover {
            color: hsl(var(--black)/.8);
        }
    }
}
.top-button {
    display: flex;
    flex-wrap: wrap;
    color: hsl(var(--dark));
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    @include md-screen {
        justify-content: center;
    }
}
// Login Registration
.login-registration-list {
    
    @include md-screen {
        padding-right: 0;
    }
    &__item {
        padding: 6px 14px;
        color: hsl(var(--black));
        position: relative;
        border-radius: 16px; 
        position: relative;
        z-index: 1;
        overflow: hidden;
        background: hsl(var(--white));
        &::before{
        content: "";
        display: block;
        border-radius: 12px; 
        position: absolute;
        background: inherit;
        top: 0.2em;
        left: 0.2em;
        right: 0.2em;
        bottom: 0.2em;
        z-index: -1;
        }

        &::after{
            content: "";
            display: block;
            border-radius: 16px; 
            position: absolute;
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;
            z-index: -2;
            transform: rotate(0deg);
            animation: gradient 2s ease alternate infinite;
            background: rgba(240,119,57,1);
            background: -moz-linear-gradient(left, rgb(51, 100, 235) 0%, rgba(40,214,98,1) 30%, rgba(240,119,57,1) 70%, rgb(227, 54, 68) 100%);
            background: -webkit-linear-gradient(left, rgba(51, 100, 235) 0%,rgba(40,214,98,1) 30%,rgba(240,119,57,1) 70%,rgb(227, 54, 68) 100%);
            background: linear-gradient(to right, rgba(51, 100, 235) 0%,rgba(40,214,98,1) 30%,rgba(240,119,57,1) 70%,rgb(227, 54, 68) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#33cbeb', endColorstr='#e3366b',GradientType=1 );
            background-size: 300% 300%;
        }
        &:hover::after{
            -webkit-animation-play-state: paused;
            animation-play-state: paused;
    }
    }
    &__icon {
        color: hsl(var(--black));
        margin-right: 10px;
        font-size: 16px;
        i {
            color: hsl(var(--black));
           
        }
    }
   &__link {
        color: hsl(var(--black));
        font-weight: 400;
        font-family: var(--heading-font);
        .icon{
            svg{
                fill: hsl(var(--black));
            }
        }
        @include msm-screen {
            font-size: 15px;
        }
        &:hover {
            color: hsl(var(--black));
            .login-registration-list__icon {
                color: hsl(var(--black));
            }
            @include md-screen {
               color: hsl(var(--black));
            }
        }
    }
}
@-webkit-keyframes gradient {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 95% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  @keyframes gradient {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 95% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
  
// Language Box
.language-box {
    display: flex;
    align-items: center;
    position: relative;
    .select {
        color: hsl(var(--black));
        background-color: transparent;
        border: 1px solid transparent;
        padding: 3px 0px;
        font-size: 16px;
        font-weight: 400;
        option {
            background-color: hsl(var(--dark));
        }
    }
}


li.login-registration-list__item .social-list__item .social-list__link {
    
    color: hsl(var(--black)) !important;
    border: 0px !important;
    &:hover {
        color: hsl(var(--base)) !important;
    }
    i {
        font-size: 15px;
        padding-right: 12px !important;
    }
}

@media(max-width:426px){
    li.login-registration-list__item{
        display: none;
    }
 
    .menu-wrapper{
        display: none;
    }
}

  
// ================ Top Header End Here ================
