/*====================== Pagination Style ====================*/
.pagination {
    justify-content: center;
    .page-item {
        .page-link{
            border: 1px solid hsl(var(--base) / .5);
            margin: 0 5px;
            width: 34px;
            height: 34px;
            border-radius: 8px;
            display: flex;
            justify-content: center;
            align-items: center;
            background-color: transparent;
            font-weight: 500;
            padding: 0;
            color: hsl(var(--black)); 
            span{
                display: flex;
                justify-content: center;
                align-items: center;
            }
            &.active , &:hover {
                background-color: hsl(var(--base));
                color: hsl(var(--white)); 
                fill:hsl(var(--white)); 
            }
            &:focus {
                box-shadow: none;
            }
        }
        &.active .page-link {
            background-color: hsl(var(--base));
            color: hsl(var(--white)); 
        }
    }
}





