.chat-box{
    width: 360px;
    height: auto;
    background: hsl(var(--white));
    position: fixed;
    bottom: 0px;
    right: 40px;
    border-radius: 4px;
    border: 1px solid hsl(var(--base-border-color));
    z-index: 1;
    display: none;
}
.chat-box-show{
    display: block;
}
.chat-box-header{
    padding: 10px 16px;
    border-bottom: 1px solid hsl(var(--base-border-color));
    display: flex;
    justify-content: space-between;
    align-items: center;
    .user-meta{
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        gap: 12px;
    }
    .user-actn{
        display: flex;
        gap: 12px;
    }
}
.hide-chat-body{
    display: none;
}
.chat-body{
.msg-list-wraper{
    max-height: 40vh;
    overflow-y: auto !important;
    .message-text{
        box-sizing: border-box;
        padding: 8px 16px;
        margin: 16px;
        background: #fff;
        border-radius: 4px 4px 4px 0;
        min-height: 32px;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        max-width: 66%;
        box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
        font-size: 14px;
        line-height: 1.5;
        word-break: break-word;
        font-size: 14px !important;
        .text{
            p{
                font-size: 14px;
            }
        }
        .msg-text-meta{
            padding: 4px 0px;
            display: flex;
            flex-direction: row;
            gap: 16px;
            font-size: 12px;
            .text-dt{
                color: #999;
            }
            span{
                i{
                    color: hsl(var(--base));
                }
            }
           
        }
       
    }

    .odd{
        margin: 16px 16px 16px auto;
        border-radius: 4px 4px 0 4px;
        background: hsl(var(--base)/.05);
        text-align: left;
       
    }
    .msg-time{
        font-size: 11.2px;
        background: #eee;
        padding: 0 16px;
        border-radius: 32px;
        color: #999;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        margin: 16px auto;
        line-height: 27px;
    }
}::-webkit-scrollbar {
    width: 4px;
  }
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px hsl(var(--base)/.2); 
    
  }
  ::-webkit-scrollbar-thumb {
    background: hsl(var(--base)/.5); 
    
  }
  ::-webkit-scrollbar-thumb:hover {
    background: hsl(var(--base)); 
  }

}


.open-message-list-btn{
    display: none;
}

@media(max-width:769px){
    .massenger{
        margin-top: 65px;
        height:91vh;
    }
    .open-message-list-btn{
        background: hsl(var(--base));
        display: block !important;
        position: absolute;
        top: 50%;
        left: 0px;
        z-index: 99 !important;
        padding: 12px;
        border-radius: 0px 50px 50px 0px;
        i{
            color: hsl(var(--white));
        }
    }
    .massenger-section{
        height: 90vh; 
    }
    .msg-list-wraper{
        height: 74vh !important;
    }
    .message-list-box{
        display: none;
    }
    .message-list-show{
        .message-list-box{
            display: block !important;
            position: absolute;
            z-index: 9;
            background: hsl(var(--white));
            max-width: 250px;
        }
        .message-list{
            height: 73.5vh !important;
        }
    }
}

@media(max-width:425px){
    .chat-box{
        width: 360px;
        bottom: 80px;
        right: 36px;
    }
}
@media(max-width:375px){
    .chat-box{
        width: 360px;
        right: 16px;
    }
}
@media(max-width:320px){
    .chat-box{
        width: 310px;
        right: 12px;
    }
}