// dash board
.dashboard{
    .dashboard_profile {
        @include lg-screen {
            padding-left: 0;
            margin-bottom: 20px;
        }
    }
    .dashboard_profile_wrap {
        position: relative;
        margin-bottom: 24px;
        h3 {
            font-size: 18px;
        }
        .profile_photo {
            height: 100px;
            width: 100px;
            border-radius: 50%;
            overflow: hidden;
            position: relative;
            left: 50%;
            transform: translateX(-50%);
            &::before {
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                background: hsl(var(--dark));
                opacity: 0;
                transition: all .4s;
                visibility: hidden;
            }
            img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            }
            .photo_upload {
                display: inline-block;
                padding: 0px 0px 0px;
                position: absolute;
                top: 50%;
                left: 50%;
                color: hsl(var(--white));
                transform: translate(-50%, -50%);
                 i{
                    cursor: pointer;
                    visibility: hidden;
                    opacity: 0;
                 }
                .upload_file {
                    opacity: 0;
                    position: absolute;
                    z-index: -1;
                    visibility: hidden;
                    display: none;
                }
            }
        }
        h3 {
            font-size: 25px;
            font-weight: 500;
            margin-bottom: 3px;
            letter-spacing: 1px;
            margin-top: 10px;
        }
        p {
        margin-bottom: 10px;
        }
        .profile-details {
            ul {
                li {
                    p {
                        span {
                            font-weight: 500;
                            font-size: 18px;
                            display: inline-block;
                        }
                        margin-bottom: 0;
                        // border-bottom: 1px solid hsl(var(--base)/0.1);
                        padding-bottom: 5px;
                    }
                }
            }
        }
    }
    .dashboard_profile_wrap .profile_photo:hover::before {
        visibility: visible;
        opacity: .7;
    }
    .dashboard_profile_wrap .profile_photo:hover .photo_upload i {
        visibility: visible;
        opacity: 1;
    }
    .dashboard_profile{
        &__details {
            ul {
                li {
                    a {
                        display: block;
                        font-size: 17px;
                        transition: all .3s ease-in-out;
                        i {
                            margin-right: 10px;
                        }
                        &:hover {
                            color: hsl(var(--base));
                        }
                        &.active {
                        color: hsl(var(--base));
                        }
                    }
    
                }
            }
        }
    }

    .dashboard-body {
        position: relative;
        &__bar {
            margin-bottom: 15px;
            display: none;
            @include lg-screen {
                display: block;
            }
            &-icon {
                color: hsl(var(--white));
                font-size: 25px;
                margin-bottom: 10px;
                cursor: pointer;
                width: 50px;
                height: 40px;
                background-color: hsl(var(--base));
                text-align: center;
                padding: 2px 8px;
                border-radius: 3px;
            }
        }
       

    }

    .selling-group-item span {
        font-size: 14px;
    }
    .message-send__content.section-bg-two {
        padding: 3px 5px;
        margin-bottom: 5px;
    }
    
    .message-send__content.section-bg-two p {
        font-size: 14px;
    }
    
    ul.list.d-flex.message-send__history {
        font-size: 12px;
    }
    
    .message-send {
        margin-bottom: 10px;
    }
    
    .message-receive__history-item {
        margin-right: 20px;
        position: relative;
    }
    
    .message-receive__history-item:last-child {
        margin-right: 0;
    }
    
    .message-receive__history-item:after {
        position: absolute;
        top: 9px;
        right: -13px;
        content: "";
        background: hsl(var(--base));
        height: 4px;
        width: 4px;
        border-radius: 50%;
    }
    .message-receive__history-item:last-child:after {
        display: none;
    }
    
    .milestones-action {
        cursor: pointer;
    }
    
    .cursor-pointer {
        cursor: pointer;
    }

    /*======= Dashboard Card =======*/
.dashboard-card { 
    box-shadow: 0px 15px 30px rgba(61, 90, 125, 0.08);
    padding: 28px;
    border: 2px solid hsl(var(--base)/0.07);
    transition: all 0.4s ease-in-out;
    text-align: start;
    position: relative;
    z-index: 1;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    border-radius: 12px;
    .banner-effect-1 {
        content: "";
        position: absolute;
        top: 42px;
        right: -4px;
        width: 80px;
        height: 80px;
        background-color: hsl(var(--base)/0.3);
        z-index: -1;
        transform: translate(-50%, -50%);
        filter: blur(55px);
    }
    &:hover {
        box-shadow: 0px 20px 30px rgba(1, 6, 20, 0.1);
        border: 2px solid hsl(var(--base)/0.4);
        transform: translateY(-3px);
    }
    // @include sm-screen {
    //     padding: 20px 15px;
    // }
    &__link {
        position: absolute;
        right: 20px;
        a {
            color: hsl(var(--white));
            &:hover {
                color: hsl(var(--base));
            }
        }
    } 
    &__thumb{
        width: 40px;
        height: 40px;
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 50%;
        }
    }
    &__icon {
        font-size: 20px;
        width: 40px;
        height: 40px;
        color: hsl(var(--white));
        background-color: hsl(var(--base));
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
    }
    &__title {
        margin-bottom: 10px;
        font-size: 17px;
    }
    &__content {
        width: calc(100% - 60px);
        padding-left: 15px;
    }   
    .notification-box {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: calc(100% - 60px);
        padding-left: 15px;
    }  
    &__amount {
        margin-bottom: 5px;
        margin-bottom: 0;
        font-size: 14px;
    }

}
.selling-group-item {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid hsl(var(--base) /.2);
    padding: 6px 0;
}

.card-header.selling-header {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
}
.card-primary {
    background-color: hsl(var(--primary)/.6);
    .dashboard-card__icon {
        background-color: hsl(var(--primary));
    }
}
.card-violet {
    background-color: hsl(var(--violet)/.6);
    .dashboard-card__icon {
        background-color: hsl(var(--violet));
    }
}
.card-success {
    background-color: hsl(var(--success)/.6);
    .dashboard-card__icon {
        background-color: hsl(var(--success));
    }
}
.card-danger {
    background-color: hsl(var(--danger)/.4);
    .dashboard-card__icon {
        background-color: hsl(var(--danger));
    }
}
.card-warning {
    background-color: hsl(var(--warning)/.6);
    .dashboard-card__icon {
        background-color: hsl(var(--warning));
    }
}
.card-info {
    background-color: hsl(var(--info)/.6);
    .dashboard-card__icon {
        background-color: hsl(var(--info));
    }
}

.card-success-1 {
    background-color: hsl(var(--success)/.3);
    .dashboard-card__icon {
        background-color: hsl(var(--success));
    }
}
.card-primary-1 {
    background-color: hsl(var(--primary)/.4);
    .dashboard-card__icon {
        background-color: hsl(var(--primary));
    }
}
.card-violet-1 {
    background-color: hsl(var(--violet)/.3);
    .dashboard-card__icon {
        background-color: hsl(var(--violet)/.8);
    }
}

.dashboard-body .single-service__icon img {
    width: 160px !important;
    padding: 5px;
    border: 3px solid hsl(var(--base)/0.06);
    height: 60px;
    object-fit: contain;
}
}

.dashboard-chart{
    padding: 20px;
    box-shadow: 0px 4px 10px rgba(1, 15, 28, 0.14);
    border-radius: 12px;
    border: 1px solid transparent;
}

// dashboard header
.dashboard-header{
    box-shadow: 0px 1px 5px rgba(1, 9, 17, 0.14);
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 8px;
    ul{
        display: inline-flex;
        gap: 12px;
        .is-open-notification{
            .notification-dropdown{
                display: block;
            }
        }
        li{
            height: 40px;
            width: 40px;
            border: 1px solid hsl(var(--base));
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;

        }
        i{
            font-size: 20px;
        }
    }
}
// responsive code
@media(max-width:426px){
    .dashboard-header{
        padding: 12px;
        border-radius: 8px;
        ul{
            gap: 8px;
            margin-left: 8px;
        }
    }
}

.search-box{
    position: relative;
    width: 300px;

    i{
        position: absolute;
        top: 16px;
        right: 20px;
    }
}

 //   notification dropdown
 .notification-dropdown{
    display: none;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 6px 10px;
    position: absolute;
    top: 44px;
    right: 0;
    width: 320px;
    height: 300px;
    max-height: 300px;
    overflow-y:scroll;
    background: hsl(var(--white)) !important;
    border-radius: 4px;
    z-index: 9;
    .notification-dropdown-menu{
        padding: 12px;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        background: hsl(var(--white)) !important;
       .notification-item-wraper{
        display: flex;
        flex-direction: column;
        gap: 8px;
        .notification-item{
            padding: 12px;
            height: 84px;
            border-bottom: 1px solid hsl(var(--base)/0.2);
            a{
                display: inline-flex;
                align-items: center;
                span{
                    border-right: 1px solid hsl(var(--dark)/.2);
                    height: 34px;
                    width: 34px;
                   
                    display: flex;
                    justify-content: center;
                    margin-right: 6px;
                    align-items: center;
                    .notification-icon{
                        font-size: 20px;
                        margin-right: 8px;
                    }
                }
                .notification-title{
                    font-size: 14px;
                    width: 230px;
                    padding: 0px 4px;
                }
            }
            &:hover{
                background: hsl(var(--base)/.2);
            }
        }

       }     
        
        .notification-item-footer{
            a{
                p{
                    font-size: 14px;
                    padding: 8px 0px;
                    color: hsl(var(--base));
                }
            }
        }
    }
   }::-webkit-scrollbar {
    width: 6px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px hsl(var(--base)); 
    
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: hsl(var(--base)/.5); 
    
  }
  
  
  ::-webkit-scrollbar-thumb:hover {
    background: hsl(var(--base)); 
  }




//   advertisement text are
.ad-text-area{
    height: 70px;
    width: 100%;
    font-weight: 500;
    outline: none;
    width: 100%;
    padding: 10px 10px;
    border-radius: 4px;
    background-color: hsl(var(--body-color)/.1); 
    border: 1px solid hsl(var(--base)/0.3) ;
    color: hsl(var(--dark)) !important; 
    line-height: 1;
    &:focus {
        color: hsl(var(--dark)); 
        box-shadow: none;
        border-color: hsl(var(--base)); 
        background-color: hsl(var(--body-color)/.05);  
    }
}