.user-profile-box{
    padding: 60px 0px 30px 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 295px;
    border-bottom: 1px solid hsl(var(--base-border-color));
    margin: 0px 0px 0px 30px;
    .button-wraper{
        display: inline-flex;
        flex-wrap: wrap;
        gap: 12px;
    }
    .user-profile-meta{
        display: inline-flex;
        gap: 15px;
        padding: 0px 0px 30px 0px;
        .user-content{
            .user-name{
                font-family: var(--heading-font);
                font-size: 20px;
                list-style: 28px;
                font-weight: 700;
               
            }
            .user-join-date{
                font-family: var(--heading-font);
                font-size: 16px;
                line-height: 20px;
                font-weight: 400;
            }
        }
    }
    .community-item-wraper{
        padding: 0px 0px 32px 0px;
        display: flex;
        justify-content: space-between;
        .community-item{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            gap: 24px;
            .item-status{
                .count{
                    color: hsl(var(--black));
                    font-weight: 500;
                    margin-bottom: 7px;
                }
                .item-status-title{
                    color: hsl(var(--text-color));
                }
            }
        }
    }
    .user-thumb{
        height: 49px;
        width: 49px;
        img{
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }
}
.user-meta{
    display: inline-flex;
    justify-content: space-between;
    width: 100%;
}
.user-social-meta{
    display: flex;
    flex-direction: column;
    gap: 16px;
    .social-link-box{
    display: inline-flex;
    gap: 16px;
    flex-wrap: nowrap;
    span{
        i{
            font-size: 20px;
        }
    }
    a{
        color:hsl(var(--text-color));
    }
    }
}

@media(max-width:1024px){
    .user-profile-box{
        width: 100%;
        padding: 120px 0px 30px 0px;
        margin: 0px 0px 0px 10px;
    }
   
}

@media(max-width:768px){
    .hidden-item{
        display: none;
    }
    .on-user-section{
        padding: 40px 0px 10px 0px !important;
    }
    .user-profile-box{
        width: 100%;
        padding: 120px 0px 30px 32px;
    }
    .user-profile-meta{
        display: inline-flex;
        gap: 15px;
        padding: 120px 0px 10px 0px;
    }
}

@media(max-width:425px){
    .on-user-side{
        padding: 0px !important;
    }
    .on-user-section{
        padding: 40px 0px 100px 0px !important;
    }
}
