// forum card
.forum-card-wraper{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    height: 100%;
    padding: 60px 74px 0px 74px;
    .forum-card{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .vote-item{
            padding-right: 17px;
            margin-left: -4px;
            display: flex;
            flex-direction: column;
            align-items: baseline;
            justify-content: flex-start;
            width: 68px;
            .vote-item-wraper{
                display: flex;
                flex-direction: column;
                align-items: center;
            }
            button {
                display: flex;
                justify-content: center;
                align-items: center;
                i{
                    font-size: 22px;
                    color: hsl(var(--grey)/0.5);
                }
            }
            .active-upvote {
                i{
                    color: hsl(var(--base)) !important;
                    
                }
            }
            .active-downvote {
                i{
                    color: hsl(var(--danger)) !important;
                }
            }
            .vote-value-container{
                font-weight: 700;
                font-size: 16px;
                display: flex;
                align-items: center;
                justify-content: center;
                .vote-qty__value{
                    font-weight: 700;
                    font-size: 16px;
                }
            }
        }
        .card--body{
            border: 1px solid hsl(var(--base-border-color));
            border-radius: 8px;
            .card-auth-meta{
                display: flex;
                justify-content: space-between;
                width: 100%;
                padding: 16px 16px 0px 16px;
                .auth-info{
                    display: inline-flex;
                    flex-wrap: wrap;
                    align-items: center;
                    gap: 7px;
                    margin-bottom: 11px;
                    i{
                        font-size: 6px;
                    }
                    a{
                        display: inline-flex;
                        align-items: center;
                        gap: 7px;
                        .user-thumb{
                            width: 24px;
                            height: 24px;
                            img{
                                border-radius: 50%;
                                width: 100%;
                                height: 100%;
                            }
                        }
                        
                    }
                }
            }
            .card-content{
                display: flex;
                flex-direction: column;
                gap: 16px;
                padding: 0px 16px;
                margin-bottom: 24px;
                .card-title{
                    font-size: 20px;
                    font-weight: 600;
                  
                }
            }

            .forum-cad-footer{
                border-top: 1px solid hsl(var(--base-border-color));
                display: inline-flex;
                width: 100%;
                justify-content: space-between;
                align-items: center;
                padding: 12px 16px;
                .footer-item-list{
                    display: inline-flex;
                    flex-wrap: wrap;
                    width: 100%;
                    gap: 16px;
                    li{
                        display: flex;
                        flex-wrap: wrap;
                        align-items: center;
                        padding: 0px 4px 0px 0px;
                        a{
                            display: inline-flex;
                            flex-wrap: wrap;
                            align-items: center;
                            gap: 4px;
                            position: relative;
                            &::after{
                                position: absolute;
                                content: "";
                                top: 7px;
                                right: -11px;
                                width: 2px;
                                height: 10px;
                                background: hsl(var(--base-border-color));
                            }
                        }
                        
                        p{
                            font-family: var(--heading-font);
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 18px;
                        }
                    }
                }
            }
           
        }
    }
}

.actn-dropdown-box{
    position: relative;
}
.is-open-actn-dropdown{
    display: block !important;
}
.actn-dropdown-box{
    .actn-dropdown-btn{
        color: hsl(var(--text-color));
    }
}
.actn-dropdown{
    position: absolute;
    top: -8px;
    width: 243px;
    background-color: hsl(var(--white));
    display: none;
    border-radius: 5px;
    box-shadow: 0px 20px 50px 0px #2C3F5826;
    padding: 8px 0px;
    z-index: 9;
    ul{
        li{
            width: 100%;

            
            button{
                display: inline-flex;
                align-items: center;
                gap: 16px;
                align-items: center;
                height: 41px;
                padding: 0px 0px 0px 15px;
                svg{
                    fill: hsl(var(--text-color));
                }
            }
            span{
                color:hsl(var(--text-color));
            }
            img{
                width: 20px;
                height: 20px;
            }
            &:hover{
                background: hsl(var(--base)/0.2);
                color: hsl(var(--base)) !important;
                span{
                    color: hsl(var(--base)) !important;
                }
            }
        }
    }
}

.fa-circle-up{
    &:hover{
        color: hsl(var(--base)) !important;
    }
}

.fa-circle-down{
    &:hover{
        color: hsl(var(--danger)) !important;
    }
}

.active-bookmark{
    i{
        color: hsl(var(--base)) !important;
    }
}

@media (max-width: 1440px) {
   
    .forum-card-wraper{
        padding: 60px 0px 0px 0px;
    }
  }

@media (max-width: 768px) {
    .option{
        margin-left: -150px;
    }
    .actn-dropdown{
        position: absolute;
        top: -8px;
        width: 166px;
        background-color: hsl(var(--white));
        display: none;
        border-radius: 5px;
        box-shadow: 0px 20px 50px 0px #2C3F5826;
        padding: 8px 0px;
        z-index: 9;
    } 
    .job{
        padding: 120px 0px 0px 10px !important;
    }
    .forum-card-wraper{
        padding: 120px 0px 0px 0px;
    }
    .vote-item{
        margin: 0;
        padding: 0px 0px 0px 20px;
        width: 82px !important;
    }

}

@media(max-width:425px){
    .vote-item{
        width: 122px !important;
    }
}

@media(max-width:320px){
    .vote-item{
        width: 156px !important;
    }
}