// left sidebar
.leftside-bar{
    border-right: 1px solid hsl(var(--base-border-color));
    height: 100%;
    display: flex;
    justify-content: flex-end;
    .menu-item-wraper{
        padding:60px 30px 0px 0px;
        width: 326px;
        i{
            font-size: 24px;
            color: hsl(var(--base));
        }
        .nav-menu{
            border-bottom: 1px solid hsl(var(--base-border-color));
            .navmenu-item-wraper{
                padding: 0px 0px 0px 55px;
                display: flex;
                flex-direction: column;
            }
        }
    }

    .latest-topics-menu{
        border-bottom: 1px solid hsl(var(--base-border-color));
        width: 100%;
        .latest-topics-wraper{
            padding: 0px 0px 0px 55px;
            width: 100%;
            margin-top: 40px;
            width: 100%;
            .latest-topics-list{
                padding: 23px 0px 0px 0px;
                display: inline-flex;
                flex-direction: column;
                width: 100%;
            }
        }
    }

    .others-menu{
        border-bottom: 1px solid hsl(var(--base-border-color));
        .others-item-wraper{
            padding: 0px 0px 0px 55px;
            margin-top: 40px;
            .others-item-list{
                padding: 23px 0px 0px 0px;
                display: inline-flex;
                flex-direction: column;
            }
        }
    }
    .mode-option{
        margin-top: 40px;
        .mode-option-list{
            width: 100%;
            padding: 0px 0px 0px 54px;
            display: inline-flex;
            justify-content: space-between;
        }
       
    }
    .menu-item{
        display: inline-flex;
        align-items: center;
        gap: 16px;
        margin-bottom: 40px;
        .active{
            color: hsl(var(--base)) !important;
        }
        .menu-name{
            color: hsl(var(--grey)/0.8);
            font-family: var(--menu-font);
            font-weight: 400;
            &:hover{
                color: hsl(var(--base));
            }
        }
        span{
            svg{
                fill: hsl(var(--base));
            }
        }
    }
    .menu-title{
        font-family: var(--menu-font);
        font-weight: 600;
        color: hsl(var(--grey)/0.8);
    }
}
.show-all{
    display: block !important;
    
}
.show-all-menu-wraper{
    display: none;
   
}
.show-all-menu-item{
    display: flex;
    flex-direction: column;
    height: 100%;
}
.show-all-tgl-btn{
    display: block;
}


@media (max-width: 1024px) {
    .leftside-bar .menu-item-wraper {
        padding: 60px 18px 0px 0px;
        width: 100%;
    }
}

@media (max-width: 768px) {
    .leftside-bar {
        display: none;
    }
}