// right sidebar
.rightside-bar{
    border-left: 1px solid hsl(var(--base-border-color));
    height: 100%;
    max-height: 100%;
    .community-state-box{
        padding:60px 0px 0px 30px;
        width: 326px;
        .community-item-wraper{
            padding: 16px 0px 40px 0px;
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid hsl(var(--base-border-color));
            .community-item{
                display: flex;
                flex-wrap: wrap;
                justify-content: space-between;
                gap: 24px;
                .item-status{
                    .count{
                        color: hsl(var(--black));
                        font-weight: 700;
                        margin-bottom: 7px;
                    }
                    .item-status-title{
                        color: hsl(var(--text-color));
                    }
                }
            }
        }
    }


    .popular-topics-box{
        padding:40px 0px 0px 30px;
        width: 326px;
    }
    .popular-card-wraper{
        padding: 16px 0px 0px 0px;
        .popular-topics-hr{
            border-top: 1px solid hsl(var(--base-border-color));
            opacity: 1;
        }
    }
    .popular-topics-card{
        width: 260px;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        gap: 16px;
        .topics-card-meta{
            display: flex;
            flex-direction: column;
            flex-wrap: wrap;
            gap: 12px;
            i{
                font-size: 6px;
            }
            .card-auth-info{
                display: inline-flex;
                align-items: center;
                gap: 7px;
                a{
                    display: inline-flex;
                    align-items: center;
                    gap: 7px;
                    img{
                        border-radius: 50%;
                    }
                }
               
            }
            .topics-card-title{
                font-size: 16px;
                line-height: 20px;
                font-weight: 600;
                
            }  
        }
        ul{
            display: inline-flex;
            flex-wrap: wrap;
            justify-content: space-between;
            width: 100%;
           
            li{
                display: inline-flex;
                flex-wrap: wrap;
                align-items: center;
                gap: 4px;
                a{
                    display: inline-flex;
                    flex-wrap: wrap;
                    align-items: center;
                    gap: 4px;
                }
                p{
                    font-family: var(--heading-font);
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 18px;
                }
            }
        }
       
    }
    
}

.post-by{
    font-family: var(--heading-font);
    font-size: 14px;
    line-height: 18px;
    span{
        font-family: var(--heading-font);
        font-size: 14px;
        line-height: 18px;
        color: hsl(var(--base));
        font-weight: 600;
    }
}

.time-status{
    color: var(--heading-color);
    font-size: 14px;
    font-family: var(--heading-font);
    font-weight: 600;
}



@media (max-width: 1024px) {
    .rightside-bar .popular-topics-card .topics-card-meta .card-auth-info {
        flex-wrap: wrap;
    }
    .rightside-bar .popular-topics-box {
        padding: 40px 0px 0px 14px;
        width: 100%;
    }
    .rightside-bar .popular-topics-card {
        width: 100%;
       
    }

    .rightside-bar .community-state-box {
        padding: 60px 0px 0px 14px;
        width: 100%;
    }
   
  }

  @media (max-width: 768px) {
    .community-item-wraper{
        justify-content: flex-start !important;
        gap: 12px;
    }
    .popular-topics-card ul{
        justify-content: flex-start !important;
        gap: 12px;
    }
}

@media (max-width: 425px) {
    .rightside-bar{
        padding: 0px 0px 100px 0px;
    }
 
}