.header-area{
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  box-shadow: 0 5px 5px rgba(0,0,0,0.02);
  background: hsl(var(--background));
  .main-nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px 10px 24px 10px;
    transition: 0.6s;
    .logo-box{
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .ham__menu{
      display: none;
      font-size: 24px;
    }
    .logo{
        img{
          max-width: 160px;
        }
    }
    nav{
      display: flex;
      justify-content: center;
      .nav-link{
        display: flex;
        gap: 28px;
        align-items: center;
        justify-content: center;
        li{
          a{
            font-size: 20px;
            font-weight: 400;
          }
        } 
    }

    .menu-right-wrapper {
      ul {
          margin-top: 4px;
          display: flex;
          justify-content: space-between;
          align-items: center;
         
      }
  }
  }
    

  }
}

.header-sticky{
  background: hsl(var(--base));
}

