.offcanvas{
  
  width: 300px !important;
  .offcanvas-header{
    .btn-close{
      opacity: 1;
    }
    .logo{
      max-width: 50%;
    }
  }
  .offcanvas-body{
    padding: 32px 0px;
    

    .user-info{
      padding: 24px 16px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 12px;
      
      a{
        h4{
          margin: 0px ;
          color: hsl(var(--white));
        }
      }
      .user-thumb{
        height: 80px;
        width: 80px;
        border-radius: 50%;
        z-index: 99;
        img{
          height: 100%;
          width: 100%;
          border-radius: 50%;
          object-fit: cover;
        }
        
      }
    }

    .side-Nav{
     
      padding: 0px 24px;
      li{
        position: relative;
        a{
          display: flex;
          align-items: center;
          font-size: 16px;
          gap: 16px;
          height: 50px;
          border-bottom: 1px solid hsl(var(--base)/.2);
          p{
            font-size: 16px;
          }
        }
       .active{
        color: hsl(var(--base));
       }
      }


    }
  }

  .search-box{
    padding: 24px 0px;
    display: flex;
    justify-content: center;
    .form--control{
      font-weight: 500;
      outline: none;
      width: 70%; 
      padding: 10px 44px;
      line-height: 1; 
    }
    .search-btn{
      padding: 10px 20px;
    }
  }

}

