}

@media (max-width: 770px) {
  .offcanvas{
    .search-box{
      padding: 16px 12px;
      display: flex;
      justify-content: center;
      .form--control{
        font-weight: 500;
        outline: none;
        width: 70%; 
        padding: 10px !important;
        background-color: hsl(var(--body-color)/0.1);
        border: 1px solid hsl(var(--base));
        padding-right: 0px !important;
        line-height: 1;
        
      }
      .search-btn{
        border: 1px solid hsl(var(--base));
        padding: 10px 10px !important;
        color: hsl(var(--base));
      }
    }
  }
}


