// forum card
.forum-details-card-wraper{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
    height: 100%;
    padding: 60px 0px 0px 0px;
    .forum-details-card{
        width: 748px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        .vote-item{
            width: 49px;
            display: flex;
            flex-direction: column;
            align-items: baseline;
            justify-content: flex-start;
            .vote-item-wraper{
                display: flex;
                flex-direction: column;
                align-items: center;
            }
            button {
                i{
                    font-size: 22px;
                    color: hsl(var(--text-color));
                }
            }
            .vote-value-container{
                font-weight: 700;
                font-size: 16px;
                display: flex;
                align-items: center;
                justify-content: center;
                .vote-qty__value{
                    font-weight: 700;
                    font-size: 16px;
                }
            }
        }
        .card--body{
            width: 695px;
            height: 100%;
            border: 1px solid hsl(var(--base-border-color));
            border-radius: 8px;
           
            .card-auth-meta{
                display: flex;
                justify-content: space-between;
                width: 100%;
                padding: 16px 16px 0px 16px;
            }
            .auth-info{
                display: inline-flex;
                align-items: center;
                gap: 7px;
                margin-bottom: 11px;
                i{
                    font-size: 6px;
                }
                a{
                    display: inline-flex;
                    align-items: center;
                    gap: 7px;
                    .user-thumb{
                        width: 24px;
                        height: 24px;
                        img{
                            height: 100%;
                            width: 100%;
                            border-radius: 50%;
                        }
                    }
                    
                }
                img{
                    border-radius: 50%;
                }
            }
            .card-content{
                display: flex;
                flex-direction: column;
                gap: 16px;
                padding: 0px 16px;
                margin-bottom: 24px;
                .card-title{
                    font-size: 20px;
                    font-weight: 700;
                }
            }

            .forum-cad-footer{
                border-top: 1px solid hsl(var(--base-border-color));
                border-bottom: 1px solid hsl(var(--base-border-color));
                display: inline-flex;
                width: 100%;
                justify-content: space-between;
                align-items: center;
                padding: 12px 16px;
                .footer-item-list{
                    display: inline-flex;
                    flex-wrap: wrap;
                    width: 100%;
                    gap: 14px;
                    li{
                        display: flex;
                        flex-wrap: wrap;
                        align-items: center;
                        padding: 0px 4px 0px 0px;
                        .footer-item{
                            display: inline-flex;
                            flex-wrap: wrap;
                            align-items: center;
                            gap: 6px;
                            position: relative;
                            &::after{
                                position: absolute;
                                content: "";
                                top: 7px;
                                right: -11px;
                                width: 2px;
                                height: 10px;
                                background: hsl(var(--base-border-color));
                            }
                            button{
                                i{
                                    color: hsl(var(--text-color));
                                }
                            }
                        }
                        
                        p{
                            font-family: var(--heading-font);
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 18px;
                        }
                    }
                }
            }

            .single-comment{
                padding: 16px 16px 0px 16px;
                .auth-info{
                    .uset-thumb{
                        width: 24px;
                        height: 24px;
                        img{
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }
                    .time-status{
                        color: hsl(var(--text-color));
                        font-weight: 500;
                    }
                }
                .comment-text{
                    width:560px;
                    margin-left: 32px;
                    position: relative;
                    &::before{
                        position: absolute;
                        content: "";
                        left: -19px;
                        width: 1px;
                        height: 100%;
                        background: hsl(var(--base-border-color));
                    }
                    .comment-card-footer{
                        display: inline-flex;
                        width: 100%;
                        justify-content: space-between;
                        align-items: center;
                        padding: 9px 0px 0px 0px;
                        .user-actn{
                            display: inline-flex;
                            flex-wrap: wrap;
                            width: 100%;
                            gap: 12px;
                            li{
                                .comment-voting{
                                    display: inline-flex;
                                    gap: 6px;
                                    button {
                                        i{
                                            color: hsl(var(--text-color));
                                        }
                                    }
                                    .active-upvote {
                                        i{
                                            color: hsl(var(--base));
                                        }
                                    }
                                    .active-downvote {
                                        i{
                                            color: hsl(var(--danger));
                                        }
                                    }
                                }
                                display: inline-flex;
                                flex-wrap: wrap;
                                align-items: center;
                                gap: 4px;
                                p{
                                    font-family: var(--heading-font);
                                    font-weight: 400;
                                    font-size: 14px;
                                    line-height: 18px;
                                }
                                .cmnt-reply-btn{
                                    display: inline-flex;
                                    gap: 4px;
                                }

                            }
                        }
                        
                    }
                }
            }
            .single-comment-replay{
                padding: 16px 16px 0px 16px;
                .comment-text{
                    margin-left: 32px;
                    padding: 0px 0px 33px  0px;
                   .comment-replay-field{
                    border: 1px solid hsl(var(--base-border-color));
                    height: 68px;
                   }
                }
            }
           
        }
        .nested-comment{
            .auth-info{
                .uset-thumb{
                    width: 24px;
                    height: 24px;
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
                .time-status{
                    color: hsl(var(--text-color));
                    font-weight: 500;
                }
            }
        }
    }
}
// nested comment wraper
.nested-comment-wraper{
    padding: 40px 0px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 24px;
    span{
        a{
            padding: 0px 0px 0px 13px;
            text-decoration: underline;
        }
    }
}

// comment in thread
.show-comment-field{
    .comment-in-replay{
        display: block;
    }
}

.active-upvote {
    i{
        color: hsl(var(--base)) !important;
    }
}
.active-downvote {
    i{
        color: hsl(var(--danger)) !important;
    }
}
.comment-in-replay{
    padding: 16px 0px 0px 0px;
    display: none;
    .comment-text{
        margin-left: 32px;
       .comment-replay-field{
        border: 1px solid hsl(var(--base-border-color));
        height: 68px;
       }
    }

}

// responsive code
@media(max-width:1024px){
    .forum-details-card-wraper .forum-details-card {
        width: 100%;
    }
    .forum-details-card-wraper .forum-details-card .card--body {
        width: 100%;
    }
    .forum-details-card-wraper .forum-details-card .card--body .single-comment .comment-text {
        width: 100%;
        margin-left: 16px;
    }
    .forum-details-card-wraper .forum-details-card .card--body .single-comment {
        padding: 16px 16px 0px 13px;
    }
    .replay{
        margin-left: 12px !important;
        width: 90% !important;
    }
    .forum-details-card-wraper{
        padding: 30px 0px 0px 0px;
    }
}

@media(max-width:768px){
    .forum-details-card-wraper{
        padding: 120px 0px 0px 0px;
    }
}

